<template>
  <div>
    <h1>Dashboard</h1>
    <button @click="logout">Logout</button>
  </div>
</template>

<script>
import { setAuthToken } from "../api/axios";

export default {
  methods: {
    logout() {
      localStorage.removeItem("token");
      setAuthToken(null);
      this.$router.push("/login");
    }
  },
  created() {
    const token = localStorage.getItem("token");
    if (token) setAuthToken(token);
  }
};
</script>
